	module.exports = {"data":{"talkJson":{"description":"Google Tech Talks\r\nApril 27, 2007\r\n\r\nABSTRACT\r\n\r\nFaith and evolution provide complementary--and sometimes conflicting--models of the world, and they also can model the adoption of programming languages. Adherents of competing paradigms, such as functional and object-oriented programming, often appear motivated by faith. Families of related languages, such as C, C++, Java, and C#, may arise from pressures of evolution. As designers of languages, adoption rates provide us with scientific data, but the belief that elegant designs are better is a matter of faith. This talk traces one concept, second-order quantification, from its inception in the symbolic logic of Frege through to the generic features introduced in Java 5, touching on features of faith and evolution. The remarkable correspondence between natural deduction and functional programming informed the design of type classes in Haskell. Generics in Java evolved directly from Haskell type classes, and are designed to support evolution from legacy code to generic code. Links, a successor to Haskell aimed at AJAX-style three-tier web applications, aims to reconcile some of the conflict between dynamic and static approaches to typing.\n\nGoogle EngEDU\nSpeaker: Philip Wadler","meta":{"duration":"PT1H6M54S"},"presenter":{"name":null},"slug":"faith-evolution-and-programming-languages","source":{"id":"8frGknO8rIg","provider":"youtube"},"tag":["googlevideo"],"title":"Faith, Evolution, and Programming Languages"},"allTalkJson":{"edges":[{"node":{"description":"Guy Steele's keynote at the 1998 ACM OOPSLA conference on \"Growing a Language\" discusses the importance of and issues associated with designing a programming language that can be grown by its users.\n\nACM OOPSLA conference\nSpeaker: Guy L. Steele Jr.","meta":{"duration":"PT53M31S"},"presenter":{"name":"Guy Steele"},"source":{"id":"_ahvzDzKdB0","provider":"youtube"},"slug":"growing-a-language-by-guy-steele","tag":["googlevideo"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/_ahvzDzKdB0/default.jpg","height":90,"width":120}},"title":"Growing a Language, by Guy Steele"}},{"node":{"description":"Jane Street Capital is a proprietary trading company that has shifted from developing software in mainstream programming languages to developing software almost entirely in OCaml, a statically typed functional programming language that has only modest industrial use. The scope of the enterprise is small but growing: Jane Street now has over 30 OCaml programmers that have collectively written hundreds of thousands of lines of OCaml code. OCaml is used for building everything from trading systems to research infrastructure to user interfaces to systems administration tools. This talk will discuss the motivations behind Jane Street's adoption of OCaml, and why we think that statically typed functional programming languages are a good ﬁt for the world of trading and ﬁnance.\n","meta":{"duration":"PT1H12M43S"},"presenter":{"name":null},"source":{"id":"hKcOkWzj0_s","provider":"youtube"},"slug":"caml-trading","tag":["googlevideo"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/hKcOkWzj0_s/default.jpg","height":90,"width":120}},"title":"Caml Trading"}}]}},"pathContext":{"slug":"faith-evolution-and-programming-languages","tags":["googlevideo"]}}

